*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
/* width */
::-webkit-scrollbar {
    width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
background: #f1f1f1; 
}

/* Handle */
::-webkit-scrollbar-thumb {
background: #afafaf; 
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
background: #555; 
}
.main{

    background-image: linear-gradient(110deg, rgba(58,60,120,1) 0%, rgba(113,150,198,0.12657563025210083) 82%), url("../backgroundMain.jpg") ;
    background-repeat: no-repeat;
    background-size: 100% 100%;
}
.topheaderMain{
    /* background-color: red; */
    width: 99vw;
    height: 100vh;
    border: 5px solid #fff;
    margin: auto;
}